import React from 'react'
import { css } from '@emotion/react'
import palette from '../../lib/palette'

export type InputProps = {
  label: string
}

function Input({ label }: InputProps) {
  return (
    <div css={block}>
      <input css={inputStyle} className="form__input" placeholder=" " />
      <label css={labelStyle} className="form__label">
        {label}
      </label>
    </div>
  )
}

export default Input

const block = css`
  position: relative;
  min-height: 2.38rem;
  border: 1px solid #cccccc;
  border-radius: 4px;

  &:focus-within {
    border: 1px solid ${palette.blue[700]};
  }

  .form__input:focus + .form__label,
  .form__input:not(:placeholder-shown).form__input:not(:focus) + .form__label {
    top: -0.5rem;
    left: 0.8rem;
    font-size: 0.75rem;
    padding: 0 4px;
    color: ${palette.blue[700]};
    z-index: 10;
  }
`

const inputStyle = css`
  position: absolute;
  font-size: 1rem;
  width: 100%;
  height: 100%;
  padding: 1rem;
  top: 0;
  left: 0;
  outline: none;
  border: none;
  background: none;
  z-index: 1;
`

const labelStyle = css`
  position: absolute;
  font-size: 1rem;
  left: 1rem;
  top: 7.5px;
  transition: 0.2s;
  background: #fff;
`
